@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.card__body {
  padding: 1.25rem;
  background-color: rgb(210, 210, 210);
  border-radius: 0rem 0rem 0.625rem 0.625rem;
}
.card__body > *:last-child {
  margin-bottom: 0;
}
.card__img-wrapper {
  position: relative;
  padding-top: 74.32%;
  overflow: hidden;
  border-radius: 0.625rem 0.625rem 0rem 0rem;
}
.card__img {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translatey(-50%) scale(1.5);
}
.card__text {
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.4375;
  color: #000000;
  transition: 0.75s;
}
.card__title {
  margin-bottom: 0.3125rem;
  font-family: "Noto Sans JP", sans-serif;
  font-family: "Noto Sans JP";
  font-style: normal;
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.45;
  color: #000000;
  margin-bottom: 0.625rem;
  transition: 0.75s;
}
.card {
  display: block;
  box-shadow: 0.9375rem 0.3125rem 0.5rem rgba(0, 0, 0, 0.3);
}